"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Switch } from "@/components/ui/switch"
import { Slider } from "@/components/ui/slider"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import {
  Settings,
  Save,
  Server,
  HardDrive,
  Globe,
  Shield,
  Users,
  MessageSquare,
  Cpu,
  Database,
  Wrench,
  AlertTriangle,
  RefreshCw,
  Download,
  Upload,
  Lock,
  Unlock
} from "lucide-react"

interface ServerSettings {
  general: {
    serverName: string
    maxPlayers: number
    motd: string
    difficulty: 'peaceful' | 'easy' | 'normal' | 'hard'
    gamemode: 'survival' | 'creative' | 'adventure' | 'spectator'
    pvp: boolean
    whitelist: boolean
  }
  performance: {
    maxMemory: number
    viewDistance: number
    simulationDistance: number
    maxTickTime: number
    entityActivationRange: {
      animals: number
      monsters: number
      misc: number
    }
  }
  worlds: {
    spawnProtection: number
    netherPortals: boolean
    endPortals: boolean
    generateStructures: boolean
    maxHeight: number
  }
  chat: {
    enableChat: boolean
    filterProfanity: boolean
    slowMode: number
    announceAchievements: boolean
  }
  security: {
    onlineMode: boolean
    preventProxy: boolean
    enforceSecureProfile: boolean
    opPermissionLevel: number
  }
}

const defaultSettings: ServerSettings = {
  general: {
    serverName: "Meu Servidor de Minecraft",
    maxPlayers: 20,
    motd: "§6Bem-vindo ao servidor!",
    difficulty: "normal",
    gamemode: "survival",
    pvp: true,
    whitelist: false
  },
  performance: {
    maxMemory: 4096,
    viewDistance: 10,
    simulationDistance: 8,
    maxTickTime: 60000,
    entityActivationRange: {
      animals: 32,
      monsters: 32,
      misc: 16
    }
  },
  worlds: {
    spawnProtection: 16,
    netherPortals: true,
    endPortals: true,
    generateStructures: true,
    maxHeight: 320
  },
  chat: {
    enableChat: true,
    filterProfanity: true,
    slowMode: 0,
    announceAchievements: true
  },
  security: {
    onlineMode: true,
    preventProxy: true,
    enforceSecureProfile: true,
    opPermissionLevel: 4
  }
}

export default function SettingsPage() {
  const [settings, setSettings] = useState<ServerSettings>(defaultSettings)
  const [restartRequired, setRestartRequired] = useState(false)
  const [saving, setSaving] = useState(false)
  const [restartDialog, setRestartDialog] = useState(false)

  const handleSave = async () => {
    setSaving(true)
    // Simular salvamento
    await new Promise(resolve => setTimeout(resolve, 1000))
    setSaving(false)
    setRestartRequired(true)
  }

  const handleRestart = async () => {
    setRestartDialog(false)
    // Implementar reinicialização do servidor
  }

  const updateSettings = (category: keyof ServerSettings, key: string, value: any) => {
    setSettings(prev => ({
      ...prev,
      [category]: {
        ...prev[category],
        [key]: value
      }
    }))
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-white">Configurações do Servidor</h1>
          <p className="text-gray-400">Gerencie todas as configurações do seu servidor</p>
        </div>
        <div className="flex items-center space-x-4">
          {restartRequired && (
            <Badge variant="destructive" className="bg-yellow-600">
              Reinicialização Necessária
            </Badge>
          )}
          <Button
            onClick={() => setRestartDialog(true)}
            variant="outline"
            className="border-yellow-600 text-yellow-500 hover:bg-yellow-600/20"
            disabled={!restartRequired}
          >
            <RefreshCw className="mr-2 h-4 w-4" />
            Reiniciar Servidor
          </Button>
          <Button 
            onClick={handleSave}
            className="bg-green-600 hover:bg-green-700"
            disabled={saving}
          >
            <Save className="mr-2 h-4 w-4" />
            {saving ? "Salvando..." : "Salvar Alterações"}
          </Button>
        </div>
      </div>

      <Tabs defaultValue="general" className="w-full">
        <TabsList className="grid grid-cols-5 bg-gray-800 w-full">
          <TabsTrigger value="general" className="data-[state=active]:bg-gray-700">
            <Server className="w-4 h-4 mr-2" />
            Geral
          </TabsTrigger>
          <TabsTrigger value="performance" className="data-[state=active]:bg-gray-700">
            <Cpu className="w-4 h-4 mr-2" />
            Performance
          </TabsTrigger>
          <TabsTrigger value="worlds" className="data-[state=active]:bg-gray-700">
            <Globe className="w-4 h-4 mr-2" />
            Mundos
          </TabsTrigger>
          <TabsTrigger value="chat" className="data-[state=active]:bg-gray-700">
            <MessageSquare className="w-4 h-4 mr-2" />
            Chat
          </TabsTrigger>
          <TabsTrigger value="security" className="data-[state=active]:bg-gray-700">
            <Shield className="w-4 h-4 mr-2" />
            Segurança
          </TabsTrigger>
        </TabsList>

        {/* General Settings */}
        <TabsContent value="general" className="space-y-4 mt-4">
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white">Configurações Gerais</CardTitle>
              <CardDescription className="text-gray-400">
                Configurações básicas do servidor
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <label className="text-sm text-gray-300">Nome do Servidor</label>
                  <Input
                    value={settings.general.serverName}
                    onChange={(e) => updateSettings('general', 'serverName', e.target.value)}
                    className="bg-gray-700 border-gray-600"
                  />
                </div>
                
                <div className="space-y-2">
                  <label className="text-sm text-gray-300">Máximo de Jogadores</label>
                  <Input
                    type="number"
                    value={settings.general.maxPlayers}
                    onChange={(e) => updateSettings('general', 'maxPlayers', parseInt(e.target.value))}
                    className="bg-gray-700 border-gray-600"
                  />
                </div>

                <div className="space-y-2">
                  <label className="text-sm text-gray-300">Dificuldade</label>
                  <Select
                    value={settings.general.difficulty}
                    onValueChange={(value: any) => updateSettings('general', 'difficulty', value)}
                  >
                    <SelectTrigger className="bg-gray-700 border-gray-600">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-gray-700 border-gray-600">
                      <SelectItem value="peaceful">Pacífico</SelectItem>
                      <SelectItem value="easy">Fácil</SelectItem>
                      <SelectItem value="normal">Normal</SelectItem>
                      <SelectItem value="hard">Difícil</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <label className="text-sm text-gray-300">Modo de Jogo</label>
                  <Select
                    value={settings.general.gamemode}
                    onValueChange={(value: any) => updateSettings('general', 'gamemode', value)}
                  >
                    <SelectTrigger className="bg-gray-700 border-gray-600">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-gray-700 border-gray-600">
                      <SelectItem value="survival">Sobrevivência</SelectItem>
                      <SelectItem value="creative">Criativo</SelectItem>
                      <SelectItem value="adventure">Aventura</SelectItem>
                      <SelectItem value="spectator">Espectador</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <label className="text-sm text-gray-300">Mensagem do Dia (MOTD)</label>
                <Input
                  value={settings.general.motd}
                  onChange={(e) => updateSettings('general', 'motd', e.target.value)}
                  className="bg-gray-700 border-gray-600"
                />
                <p className="text-xs text-gray-400">Suporta códigos de formatação (§)</p>
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">PvP</label>
                  <p className="text-xs text-gray-400">Permitir combate entre jogadores</p>
                </div>
                <Switch
                  checked={settings.general.pvp}
                  onCheckedChange={(checked) => updateSettings('general', 'pvp', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Whitelist</label>
                  <p className="text-xs text-gray-400">Apenas jogadores autorizados podem entrar</p>
                </div>
                <Switch
                  checked={settings.general.whitelist}
                  onCheckedChange={(checked) => updateSettings('general', 'whitelist', checked)}
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Performance Settings */}
        <TabsContent value="performance" className="space-y-4 mt-4">
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white">Performance</CardTitle>
              <CardDescription className="text-gray-400">
                Otimize o desempenho do servidor
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-4">
                <label className="text-sm text-gray-300">Memória Máxima (MB)</label>
                <div className="flex items-center space-x-4">
                  <Slider
                    value={[settings.performance.maxMemory]}
                    onValueChange={([value]) => updateSettings('performance', 'maxMemory', value)}
                    max={8192}
                    step={512}
                    className="flex-1"
                  />
                  <span className="text-white w-20 text-right">{settings.performance.maxMemory}MB</span>
                </div>
              </div>

              <div className="space-y-4">
                <label className="text-sm text-gray-300">Distância de Renderização</label>
                <div className="flex items-center space-x-4">
                  <Slider
                    value={[settings.performance.viewDistance]}
                    onValueChange={([value]) => updateSettings('performance', 'viewDistance', value)}
                    max={32}
                    className="flex-1"
                  />
                  <span className="text-white w-20 text-right">{settings.performance.viewDistance} chunks</span>
                </div>
              </div>

              <div className="space-y-4">
                <label className="text-sm text-gray-300">Distância de Simulação</label>
                <div className="flex items-center space-x-4">
                  <Slider
                    value={[settings.performance.simulationDistance]}
                    onValueChange={([value]) => updateSettings('performance', 'simulationDistance', value)}
                    max={32}
                    className="flex-1"
                  />
                  <span className="text-white w-20 text-right">{settings.performance.simulationDistance} chunks</span>
                </div>
              </div>

              <div className="space-y-2">
                <label className="text-sm text-gray-300">Alcance de Ativação de Entidades</label>
                <div className="grid grid-cols-3 gap-4">
                  <div className="space-y-2">
                    <label className="text-xs text-gray-400">Animais</label>
                    <Input
                      type="number"
                      value={settings.performance.entityActivationRange.animals}
                      onChange={(e) => updateSettings('performance', 'entityActivationRange', {
                        ...settings.performance.entityActivationRange,
                        animals: parseInt(e.target.value)
                      })}
                      className="bg-gray-700 border-gray-600"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="text-xs text-gray-400">Monstros</label>
                    <Input
                      type="number"
                      value={settings.performance.entityActivationRange.monsters}
                      onChange={(e) => updateSettings('performance', 'entityActivationRange', {
                        ...settings.performance.entityActivationRange,
                        monsters: parseInt(e.target.value)
                      })}
                      className="bg-gray-700 border-gray-600"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="text-xs text-gray-400">Diversos</label>
                    <Input
                      type="number"
                      value={settings.performance.entityActivationRange.misc}
                      onChange={(e) => updateSettings('performance', 'entityActivationRange', {
                        ...settings.performance.entityActivationRange,
                        misc: parseInt(e.target.value)
                      })}
                      className="bg-gray-700 border-gray-600"
                    />
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* World Settings */}
        <TabsContent value="worlds" className="space-y-4 mt-4">
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white">Configurações dos Mundos</CardTitle>
              <CardDescription className="text-gray-400">
                Gerencie as configurações dos mundos
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-4">
                <label className="text-sm text-gray-300">Proteção do Spawn</label>
                <div className="flex items-center space-x-4">
                  <Slider
                    value={[settings.worlds.spawnProtection]}
                    onValueChange={([value]) => updateSettings('worlds', 'spawnProtection', value)}
                    max={64}
                    className="flex-1"
                  />
                  <span className="text-white w-20 text-right">{settings.worlds.spawnProtection} blocos</span>
                </div>
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Portais do Nether</label>
                  <p className="text-xs text-gray-400">Permitir criação de portais</p>
                </div>
                <Switch
                  checked={settings.worlds.netherPortals}
                  onCheckedChange={(checked) => updateSettings('worlds', 'netherPortals', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Portais do End</label>
                  <p className="text-xs text-gray-400">Permitir criação de portais</p>
                </div>
                <Switch
                  checked={settings.worlds.endPortals}
                  onCheckedChange={(checked) => updateSettings('worlds', 'endPortals', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Gerar Estruturas</label>
                  <p className="text-xs text-gray-400">Vilas, templos, fortalezas, etc.</p>
                </div>
                <Switch
                  checked={settings.worlds.generateStructures}
                  onCheckedChange={(checked) => updateSettings('worlds', 'generateStructures', checked)}
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Chat Settings */}
        <TabsContent value="chat" className="space-y-4 mt-4">
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white">Configurações do Chat</CardTitle>
              <CardDescription className="text-gray-400">
                Gerencie as configurações do chat
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Chat Ativado</label>
                  <p className="text-xs text-gray-400">Permitir mensagens no chat</p>
                </div>
                <Switch
                  checked={settings.chat.enableChat}
                  onCheckedChange={(checked) => updateSettings('chat', 'enableChat', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Filtro de Palavrões</label>
                  <p className="text-xs text-gray-400">Censurar palavras inadequadas</p>
                </div>
                <Switch
                  checked={settings.chat.filterProfanity}
                  onCheckedChange={(checked) => updateSettings('chat', 'filterProfanity', checked)}
                />
              </div>

              <div className="space-y-2">
                <label className="text-sm text-gray-300">Modo Lento (segundos)</label>
                <Input
                  type="number"
                  value={settings.chat.slowMode}
                  onChange={(e) => updateSettings('chat', 'slowMode', parseInt(e.target.value))}
                  className="bg-gray-700 border-gray-600"
                />
                <p className="text-xs text-gray-400">0 = desativado</p>
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Anunciar Conquistas</label>
                  <p className="text-xs text-gray-400">Mostrar conquistas no chat</p>
                </div>
                <Switch
                  checked={settings.chat.announceAchievements}
                  onCheckedChange={(checked) => updateSettings('chat', 'announceAchievements', checked)}
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Security Settings */}
        <TabsContent value="security" className="space-y-4 mt-4">
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white">Configurações de Segurança</CardTitle>
              <CardDescription className="text-gray-400">
                Gerencie as configurações de segurança do servidor
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Modo Online</label>
                  <p className="text-xs text-gray-400">Verificar contas originais</p>
                </div>
                <Switch
                  checked={settings.security.onlineMode}
                  onCheckedChange={(checked) => updateSettings('security', 'onlineMode', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Prevenir Proxy</label>
                  <p className="text-xs text-gray-400">Bloquear VPNs e proxies</p>
                </div>
                <Switch
                  checked={settings.security.preventProxy}
                  onCheckedChange={(checked) => updateSettings('security', 'preventProxy', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <div className="space-y-0.5">
                  <label className="text-sm text-gray-300">Perfis Seguros</label>
                  <p className="text-xs text-gray-400">Exigir perfis criptografados</p>
                </div>
                <Switch
                  checked={settings.security.enforceSecureProfile}
                  onCheckedChange={(checked) => updateSettings('security', 'enforceSecureProfile', checked)}
                />
              </div>

              <div className="space-y-2">
                <label className="text-sm text-gray-300">Nível de Permissão OP</label>
                <Select
                  value={settings.security.opPermissionLevel.toString()}
                  onValueChange={(value) => updateSettings('security', 'opPermissionLevel', parseInt(value))}
                >
                  <SelectTrigger className="bg-gray-700 border-gray-600">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent className="bg-gray-700 border-gray-600">
                    <SelectItem value="1">Nível 1 (Moderador)</SelectItem>
                    <SelectItem value="2">Nível 2 (GameMaster)</SelectItem>
                    <SelectItem value="3">Nível 3 (Admin)</SelectItem>
                    <SelectItem value="4">Nível 4 (Owner)</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Restart Dialog */}
      <Dialog open={restartDialog} onOpenChange={setRestartDialog}>
        <DialogContent className="bg-gray-800 border-gray-700">
          <DialogHeader>
            <DialogTitle className="text-white">Reiniciar Servidor</DialogTitle>
            <DialogDescription className="text-gray-400">
              O servidor será reiniciado para aplicar as alterações. Todos os jogadores serão desconectados.
            </DialogDescription>
          </DialogHeader>

          <div className="bg-yellow-600/20 border border-yellow-600/40 rounded p-3">
            <div className="flex items-start space-x-3">
              <AlertTriangle className="h-5 w-5 text-yellow-500 mt-0.5" />
              <div className="space-y-1">
                <p className="text-sm font-medium text-yellow-500">
                  Aviso de Reinicialização
                </p>
                <p className="text-sm text-yellow-500/80">
                  Certifique-se de que todos os jogadores estejam em locais seguros antes de reiniciar.
                </p>
              </div>
            </div>
          </div>

          <DialogFooter>
            <Button variant="ghost" onClick={() => setRestartDialog(false)}>
              Cancelar
            </Button>
            <Button
              onClick={handleRestart}
              className="bg-yellow-600 hover:bg-yellow-700"
            >
              <RefreshCw className="mr-2 h-4 w-4" />
              Confirmar Reinicialização
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
